<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入姓名、手机号、用户UID"
                      @keyup.native.enter="handleFilter"
                      v-model="listQuery.keyword">
            </el-input>
            <el-button class="filter-item" @click="handleFilter" size="small" style="margin-left: 30px">查询</el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间"
                    style="width: 100%; ">
                <el-table-column type="index" label="序号" width="80"></el-table-column>
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
            </el-table>
            <div v-show="!listLoading" class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="listQuery.page"
                               :page-sizes="[15,30,45,60]" :page-size="listQuery.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import DCTP from '@/utils/dctp';
    import {introlinfolist} from '@/api/useraddress';
    import calc from "@/utils/calculate-big-num";
    export default {
        data() {
            return {
                //总条数
                total: null,
                //返回来列表的数据
                tableData: [],
                listLoading: true,
                listQuery: {
                    keyword: '',
                    page: 1,
                    pageSize: 15
                },
                tableHeader: [
                    {
                        label: "用户UID",
                        prop: "userId",
                    }, {
                        label: "登录名",
                        prop: "phone",
                    }, {
                        label: "昵称",
                        prop: "realname",
                    }, {
                        label: "真实姓名",
                        prop: "userName",
                    }, {
                        label: "货币名称",
                        prop: "coinName",
                    }, {
                        label: "收益名称",
                        prop: "title",
                    }, {
                        label: "收益数量",
                        prop: "amount",
                        formatter(row, column, value) {
                            return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                        }
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }
                ]
            }

        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {
            getList() {
                this.listLoading = true;
                introlinfolist(this.listQuery).then(response => {
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                        this.listLoading = false;
                    } else {
                        this.tableData = response.data.list;
                        this.total = response.data.total;
                        this.listLoading = false;
                    }
                })
            },
            /**
             * 条件筛选查询对象
             */
            handleFilter() {
                this.getList();
            },
            //获取当前页数
            userPage(val) {
                this.listQuery.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.listQuery.pageSize = val;
                this.getList();
            }

        },
        created() {
            this.getList();
        }
    }
</script>



